// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'

import router from'./router/memberRouter.js'      //---------
import Base from './assets/js/baseFun.js'         //---------
import stores from './store/store'                //---------
import './assets/css/base.css'                    //---------css

FastClick.attach(document.body)
Vue.config.productionTip = false

//
Vue.prototype.baseFun=Base.baseFun;  //-----vuejs
Vue.prototype.baseAjax=Base.baseAjax;//-----ajaxvue 

Vue.use(VueRouter)

var globalVm=new Vue({
	router,            //-----router
    el: '#app',
    store:stores,          //-----
    template: '<App/>',
    components: { App }
})



